@import '~assets/styles/h3Imports';

.progress-stepper {
  margin: 0 -3px;
}

.progress-stepper-header {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 4px 3px;
}

.progress-stepper-counter,
.progress-stepper-title {
  color: var(--gray-4, #414646);
  font-size: rem(14);
}

.progress-stepper-body {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 4px 0;
}

.progress-stepper-button {
  border: 2px solid var(--accent-interactive, #0082aa);
  border-radius: 50%;
  flex: 0 0 14px;
  height: 14px;
  margin: 0 3px;
  padding: 0;
  transition: 0.5s ease;
  width: 14px;
}

.progress-stepper-button-active {
  background-color: var(--accent-interactive, #0d6d5e);
  border-color: transparent;
}

.progress-stepper-line {
  background-color: var(--accent-interactive, #0082aa);
  flex: 1;
  height: 2px;
}
